html,
body {
  height: 100%;
}

.gg-scroll {
  overflow: auto;
}

.gg-text-no-overflow {
  display: block;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.gg-main-body {
  height: calc(100% - 69px - 34px);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.gg-main-footer {
  font-size: 1.5rem;
  font-weight: normal;
}

.gg-aside-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.gg-aside-body {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  overflow: auto;
}

@media (min-width: 768px) {
  .gg-main-body {
    height: calc(100% - 65px);
  }
}



