.gg-interactiveMap {
  -webkit-transition: -webkit-box-shadow 500ms ease-in-out;
  transition: -webkit-box-shadow 500ms ease-in-out;
  -o-transition: box-shadow 500ms ease-in-out;
  transition: box-shadow 500ms ease-in-out;
  transition: box-shadow 500ms ease-in-out, -webkit-box-shadow 500ms ease-in-out;
}

.gg-interactiveMap_success {
  -webkit-animation: gg-showSuccess ease-in 360ms;
          animation: gg-showSuccess ease-in 360ms;
}

.gg-interactiveMap_error {
  -webkit-animation: gg-showError ease-in 360ms;
          animation: gg-showError ease-in 360ms;
}

.gg-markerIcon {
  background-size: contain;
  background-repeat: no-repeat;
}

.gg-markerSpot {
  border-radius: 100%;
  -webkit-box-shadow: 3px 3px 3px 0 rgba(120, 120, 120, .75);
          box-shadow: 3px 3px 3px 0 rgba(120, 120, 120, .75);
}

.gg-markerIcon_red {
  background-image: url('icons/marker-red.png');
}

@-webkit-keyframes gg-showSuccess {
  0%, 90% {
    -webkit-box-shadow: 0 0 3px 1px transparent;
            box-shadow: 0 0 3px 1px transparent;
  }
  30% {
    -webkit-box-shadow: 0 0 8px 4px rgba(92, 184, 92, .5);
            box-shadow: 0 0 8px 4px rgba(92, 184, 92, .5);
  }
}

@keyframes gg-showSuccess {
  0%, 90% {
    -webkit-box-shadow: 0 0 3px 1px transparent;
            box-shadow: 0 0 3px 1px transparent;
  }
  30% {
    -webkit-box-shadow: 0 0 8px 4px rgba(92, 184, 92, .5);
            box-shadow: 0 0 8px 4px rgba(92, 184, 92, .5);
  }
}

@-webkit-keyframes gg-showError {
  0%, 90% {
    -webkit-box-shadow: 0 0 3px 1px transparent;
            box-shadow: 0 0 3px 1px transparent;
  }
  30% {
    -webkit-box-shadow: 0 0 8px 4px rgba(217, 83, 79, .5);
            box-shadow: 0 0 8px 4px rgba(217, 83, 79, .5);
  }
}

@keyframes gg-showError {
  0%, 90% {
    -webkit-box-shadow: 0 0 3px 1px transparent;
            box-shadow: 0 0 3px 1px transparent;
  }
  30% {
    -webkit-box-shadow: 0 0 8px 4px rgba(217, 83, 79, .5);
            box-shadow: 0 0 8px 4px rgba(217, 83, 79, .5);
  }
}



